html, body, #root {
  height: 100%;
  margin: 0px;
}
@-webkit-keyframes highlight {
  from {
    background-color: var(--highlight);
  }
  to {
    background-color: inherit;
  }
}
@keyframes highlight {
  from {
    background-color: var(--highlight);
  }
  to {
    background-color: inherit;
  }
}



